<template>
    <div class="mainNavbar">
        <div class="navLogo">
            <router-link to="/"><span style="color: black">RBX</span> SWAG</router-link>
        </div>
        <div class="navItems">
            <router-link to="/earn">Earn</router-link>
            <router-link to="/daily">Daily Rewards</router-link>
            <router-link to="/giveaway">Giveaway</router-link>
            <router-link to="/promocode">Promocode</router-link>
            <router-link to="/payout">Payout</router-link>
            <router-link to="/account">Account</router-link>
            <router-link to="/about">About</router-link>
            <router-link to="/faq">FAQ</router-link>
        </div>
        <div v-if="loggedIn" class="account">
            <div class="icon"></div>
            <div class="name">{{ username }}</div>
            <div class="balance">{{ `${this.$siteConfig.pointsPrefix} ${balance}`}}</div>
            <button class="logout" @click="logout()">Logout</button>
        </div>
        <router-link v-else class="account" to="/login">Add Account</router-link>
    </div>
</template>

<script>
export default {
    data() {
        return {
            loggedIn: localStorage.username ? true : false,
            siteName: this.$siteConfig.siteName,
            logoUrl: this.$siteConfig.logoUrl,
            username: localStorage.username,
            balance: 0
        }
    },
    methods: {
        logout() {
            localStorage.removeItem('username')
            this.loggedIn = false
            this.$router.push('/')
        },
        fetchBalance() {
            return new Promise(async (resolve, reject) => {
                try {
                    resolve(1)
                }
                catch {
                    reject(0)
                }
            })
        }
    },
    watch: {
        async $route() {
            this.loggedIn = localStorage.username ? true : false
            this.username = localStorage.username
            this.balance = await this.fetchBalance()
        }
    },
    async mounted() {
        this.balance = await this.fetchBalance()
    }
}
</script>
